import React, { useState } from "react";
import {
  Button,
  Header,
  Icon,
  Rating,
  Modal,
  Dropdown,
} from "semantic-ui-react";
import "./Modal.css";

function ModalExampleCloseIcon(props) {
  const [review, setReview] = useState({ name: "", email: "", review: "" });
  const [group, setGroup] = useState({
    technology_Name: "",
    technology_link: "",
    about: "",
  });

  const [profile, setProfile] = useState({
    myPhoto: "",
    Name: "",
    bio: "",
    website: "",
    github: "",
    twitter: "",
    instagram: "",
    facebook: "",
    phone: "",
    mobile: "",
    address: "",
  });
  const [open, setOpen] = useState(false);

  const handleChangeInput = (e, type) => {
    e.preventDefault();
    if (type === "reviews") {
      const { name, value } = e.target;
      setReview((prevReview) => {
        return {
          ...prevReview,
          [name]: value,
        };
      });
    } else if (type === "profile") {
      const { name, value } = e.target;
      setProfile((prevProfile) => {
        return {
          ...prevProfile,
          [name]: value,
        };
      });
    } else if (type === "groups") {
      const { name, value } = e.target;
      setGroup((prevGroup) => {
        return {
          ...prevGroup,
          [name]: value,
        };
      });
    } else {
    }
  };

  const addingReviewLayout = () => {
    return (
      <div className="addingReview">
        Name
        <input
          type="text"
          name="name"
          placeholder="Name"
          onChange={(e) => {
            handleChangeInput(e, "reviews");
          }}
          value={review.name}
        />
        Email
        <input
          type="text"
          name="email"
          placeholder="Email"
          onChange={(e) => {
            handleChangeInput(e, "reviews");
          }}
          value={review.email}
        />
        Review
        <textarea
          type="text"
          name="review"
          placeholder="Type your review here"
          onChange={(e) => {
            handleChangeInput(e, "reviews");
          }}
          value={review.review}
        />
        <div className="addingReview__rating">
          <p>Ratings</p>
          <Rating maxRating={5} defaultRating={0} icon="heart" size="large" />
        </div>
      </div>
    );
  };
  const addingTechnologyLayout = () => {
    // console.log(group);
    return (
      <div className="addingReview">
        Technology Name
        <input
          type="text"
          name="technology_Name"
          placeholder="Enter technology name"
          onChange={(e) => {
            handleChangeInput(e, "groups");
          }}
          value={group.technology_Name}
        />
        Technology Link
        <input
          type="text"
          name="technology_link"
          placeholder="Enter the link"
          onChange={(e) => {
            handleChangeInput(e, "groups");
          }}
          value={group.technology_link}
        />
        Type
        <select
          onChange={(e) => {
            handleChangeInput(e, "groups");
          }}
          name="about"
          value={group.about}
        >
          <option>None</option>
          {props.options.map((option) => {
            return <option>{option.value}</option>;
          })}
        </select>
      </div>
    );
  };
  const addingEditProfile = () => {
    return (
      <div className="addingReview">
        Photo <input type="file" name="myPhoto" />
        Name
        <input
          type="text"
          name="Name"
          placeholder="Enter your name"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.Name}
        />
        Bio
        <input
          type="text"
          name="bio"
          placeholder="Enter your bio"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.bio}
        />
        Website Link
        <input
          type="text"
          name="website"
          placeholder="Enter website link"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.website}
        />
        Github Link
        <input
          type="text"
          name="github"
          placeholder="Enter github link"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.github}
        />
        Twitter Link
        <input
          type="text"
          name="twitter"
          placeholder="Enter twitter link"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.twitter}
        />
        Instagram Link
        <input
          type="text"
          name="instagram"
          placeholder="Enter instagram link"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.instagram}
        />
        Facebook Link
        <input
          type="text"
          name="facebook"
          placeholder="Enter facebook link"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.facebook}
        />
        Phone Number
        <input
          type="text"
          name="phone"
          placeholder="Enter phone number"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.phone}
        />
        Mobile Number
        <input
          type="text"
          name="mobile"
          placeholder="Enter mobile number"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.mobile}
        />
        Address
        <input
          type="text"
          name="address"
          placeholder="Enter your address"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.address}
        />
      </div>
    );
  };
  return (
    <Modal
      closeIcon
      dimmer={"blurring"}
      style={{
        height: "max-content",
        left: "auto",
        right: "auto",
      }}
      open={open}
      trigger={props.trigger}
      onClose={() => setOpen(false)}
      onOpen={() => setOpen(true)}
    >
      <Header
        icon="add"
        content={
          props.type === "reviews"
            ? "Add your review"
            : props.type === "editProfile"
            ? "Edit Profile"
            : "Add your community"
        }
      />
      <Modal.Content scrolling>
        {props.type === "reviews"
          ? addingReviewLayout()
          : props.type === "editProfile"
          ? addingEditProfile()
          : addingTechnologyLayout()}
      </Modal.Content>
      <Modal.Actions>
        <Button color="red" onClick={() => setOpen(false)}>
          <Icon name="remove" /> Decline
        </Button>
        <Button
          color="green"
          onClick={() => {
            {
              props.type === "reviews"
                ? props.revertingFCN(review)
                : props.type === "editProfile"
                ? props.revertingFCN(profile)
                : props.revertingFCN(group);
            }
            setOpen(false);
          }}
        >
          <Icon name="checkmark" /> Submit
        </Button>
      </Modal.Actions>
    </Modal>
  );
}

export default ModalExampleCloseIcon;
